@import "../components/_variables";

.section__skills {
  height: 100vh;
  background-color: $yellow;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5%;
}

.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 70%;
  max-width: 900px;
  padding-left: 50px;
  //box-shadow: 5px 5px 5px 8px rgba(182, 18, 141, 0.11);
}
.codeSkills {
  font-size: 18px;
  height: 200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@-webkit-keyframes load {
  0% {
    stroke-dashoffset: 0;
  }
}
@-moz-keyframes load {
  0% {
    stroke-dashoffset: 0;
  }
}
@keyframes load {
  0% {
    stroke-dashoffset: 0;
  }
}
.progress > li {
  display: inline-block;
  position: relative;
  text-align: center;
  color: white;
  font-weight: 900;
}

.progress > li:before {
  content: attr(data-name);
  position: absolute;
  width: 100%;
  bottom: -2rem;
  font-weight: bold;
}

.progress > li:after {
  content: attr(data-percent);
  position: absolute;
  width: 100%;
  top: 3.7rem;
  left: 0;
  font-size: 2rem;
  text-align: center;
}

.progress svg {
  width: 10rem;
  height: 10rem;
}

.progress svg:nth-child(2) {
  position: absolute;
  left: 0;
  top: 0;
  transform: rotate(-90deg);
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
}

.progress svg:nth-child(2) path {
  fill: none;
  stroke-width: 16;
  stroke-dasharray: 690;
  stroke: #00c8c8;
  -webkit-animation: load 10s;
  -moz-animation: load 10s;
  -o-animation: load 10s;
  animation: load 10s;
}

